<template>
  <div class="order-info">
    <div class="top clearfix">
      <p class="title">{{orderInfo.name}}</p>
      <p class="number">订单编号：{{orderInfo.sn}}</p>
    </div>
    <div class="order-info-wrapper clearfix">
      <div class="sec sec-tbr">
        <p class="name">投保人信息</p>
        <div class="info-con">
          <p class="item"><i>姓名：</i><span>{{tbrInfo.name}}</span></p>
          <p class="item"><i>性别：</i><span>{{getSexByName(tbrInfo.gender)}}</span></p>
          <p class="item"><i>证件类型：</i><span>身份证</span></p>
          <p class="item"><i>证件号码：</i><span>{{tbrInfo.id_num}}</span></p>
          <p class="item"><i>出生日期：</i><span>{{tbrInfo.birth}}</span></p>
        </div>
      </div>
      <div class="sec sec-bbxr">
        <p class="name">被保险人信息</p>
        <div class="info-con">
          <p class="item"><i>姓名：</i><span>{{bbxrInfo.name}}</span></p>
          <p class="item"><i>性别：</i><span>{{getSexByName(bbxrInfo.gender)}}</span></p>
          <p class="item"><i>证件类型：</i><span>身份证</span></p>
          <p class="item"><i>证件号码：</i><span>{{bbxrInfo.id_num}}</span></p>
          <p class="item"><i>出生日期：</i><span>{{bbxrInfo.birth}}</span></p>
        </div>
      </div>
      <div class="sec sec-syr">
        <p class="name">受益人信息</p>
        <div class="info-con">
          <p class="item-syr"><i>受益人：</i><span>{{orderInfo.beneficiary}}</span></p>
        </div>
      </div>
    </div>
    <div class="order-info-wrapper">
      <div class="sec sec-bzfa">
        <p class="name">保障方案</p>
        <div class="info-con-wrapper clearfix">
          <div class="fl plan-con">
            <p class="product-title" v-if="bzfaInfo.plan_name">
              {{orderInfo.name}}——{{bzfaInfo.plan_name}}
            </p>
            <p class="bzxm clearfix">
              <span class="fl">保障项目</span>
              <i class="fr">保障金额</i>
            </p>
            <ul>
              <li class="clearfix" v-for="(item,index) in bzfaInfo.plan_details" :key="index">
                <span>{{item.name}}</span>
                <i>{{item.value}}</i>
              </li>
            </ul>
          </div>
          <div class="fl bz-date">
            <p class="date clearfix"><span>保险期间</span><i>{{orderInfo.insurance_period}}</i></p>
            <p class="date clearfix"><span>起始日期</span><i>{{orderInfo.policy_effective_date}}</i></p>
            <p class="date clearfix"><span>终止日期</span><i>{{orderInfo.policy_expiration_date}}</i></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSexByName } from '@/assets/js/mixins/base.js'

export default {
  name: 'TheOrderComfirmInfo',
  mixins: [getSexByName],
  props: {
    orderInfo: {
      type: Object,
      default: () => {
      }
    },
    tbrInfo: {
      type: Object,
      default: () => {
      }
    },
    bbxrInfo: {
      type: Object,
      default: () => {
      }
    },
    bzfaInfo: {
      type: Object,
      default: () => {
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .order-info {
    float: left;
    width: 736px;
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(219, 219, 219, 0.35);
    padding: 20px 20px 8px 20px;
    .top {
      margin-bottom: 25px;
      .title {
        float: left;
        font-size: 20px;
        color: $color-text;
        letter-spacing: 0;
        font-weight: bold;
      }
      .number {
        float: right;
        font-size: 14px;
        color: #B6BBBB;
      }
    }
    .order-info-wrapper {
      .sec {
        float: left;
        border: 1px solid #EDEDED;
        border-radius: 2px;
        padding-left: 20px;
        box-sizing: border-box;
        float: left;
        margin-bottom: 12px;
        .name {
          position: relative;
          font-size: 14px;
          color: #FF703F;
          border-bottom: 1px solid #EDEDED;
          font-weight: bold;
          height: 44px;
          line-height: 44px;
          &:after {
            position: absolute;
            content: '';
            width: 56px;
            height: 2px;
            background: #FF703F;
            border-radius: 1px;
            top: 44px;
            left: 0;
          }
        }
        .info-con {
          padding: 19px 0 5px;
          .item {
            margin-bottom: 14px;
            font-size: 14px;
            color: $color-text;
            i {
              display: inline-block;
              width: 70px;
            }
          }
          .item-syr {
            i {
              width: 56px;
            }
          }
        }
        .plan-con {
          width: 413px;
          padding-right: 20px;
          border-right: 1px solid #EDEDED;
          padding-bottom: 14px;
          .product-title {
            padding-top: 14px;
            font-size: 14px;
            color: $color-text;
          }
          .bzxm {
            font-size: 14px;
            color: #B6BBBB;
            margin: 12px 0;
          }
          ul {
            li {
              margin-bottom: 14px;
              font-size: 14px;
              color: $color-text;
              span {
                float: left;
                display: block;
              }
              i {
                float: right;
                display: block;
              }
            }
          }
        }
        .bz-date {
          width: 240px;
          padding: 20px 20px 0 20px;
          font-size: 14px;
          color: $color-text;
          .date {
            margin-bottom: 24px;
            span {
              float: left;
            }
            i {
              float: right;
            }
          }
        }
      }
      .sec-tbr, .sec-bbxr {
        width: 280px;
        margin-right: 12px;
        height: 210px;
      }
      .sec-syr {
        width: 152px;
        height: 210px;
      }
      .sec-bzfa {
        width: 736px;
      }
    }
  }
</style>
