    bottom: 0;
    z-index: 19951105;
    .cover {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #333333;
      opacity: .58;
      z-index: 1;
    }
    .tipBox {
      background: #FFFFFF;
      width: 400px;
      position: absolute;
      left: 50%;
      top: 50%;
      margin: -182px -220px;
      padding: 20px;
      z-index: 2;
      border-radius: 4px;
      .p-b {
        font-size: 16px;
        .blue {
          color: #29A1F7;
        }
      }
      .p-s {
        font-size: 12px;
        margin: -2px 0 18px;
      }
      .p-b, .p-s {
        color: #2A3449;
