<template>
  <div class="order-comfirm layout clearfix">
    <order-comfirm-info
      :orderInfo="orderInfo"
     :tbrInfo="tbrInfo"
     :bbxrInfo="bbxrInfo"
      :bzfaInfo="bzfaInfo"
    ></order-comfirm-info>
    <order-comfirm-pay  :orderInfo="orderInfo"></order-comfirm-pay>
  </div>
</template>

<script>
import orderComfirmInfo from '@/components/order/TheOrderComfirmInfo'
import orderComfirmPay from '@/components/order/TheOrderComfirmPay'

export default {
  name: 'TheOrderInsured',
  data () {
    return {
      orderInfo: {},
      tbrInfo: {},
      bbxrInfo: {},
      bzfaInfo: {}
    }
  },
  components: {
    orderComfirmInfo,
    orderComfirmPay
  },
  mounted () {
    this.getOrderInfo()
  },
  methods: {
    getOrderInfo () {
      let postData = {
        sn: this.$route.query.sn
      }

      this.$api.product.getOrderInfo(postData).then(res => {
        if (res.code === 1) {
          this.orderInfo = res.data
          this.tbrInfo = this.orderInfo.member_info
          this.bbxrInfo = this.orderInfo.relation_info
          this.bzfaInfo = this.orderInfo.plan_info
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .order-comfirm {
    margin-top: 12px;
    margin-bottom: 20px;
  }
</style>
