<template>
  <div class="order-pay ">
    <p class="title">选择支付方式并付款</p>
    <ul>
      <!--<li>
          <input type="radio" value="1"  id="wechat" v-model="sex">
         <label for="wechat"><img src="../../assets/images/wechat.png" alt="wechat">微信</label>
      </li>-->
      <li>
        <input type="radio" id="zfb" value="zfb" v-model="payWay">
        <label for="zfb"><img src="../../assets/images/zfb.png" alt="zfb">支付宝</label>
      </li>
    </ul>
    <div class="tips clearfix">
      <p class="read">
        <input type="checkbox" value="3" v-model="read" id="read">
        <label for="read"></label>
      </p>
      <p class="protocal">我已阅读本产品 <span @click="goProtocalList">《保险条款》</span><span @click="notifyShow=true">《客户告知书》</span>等信息，并充分理解和接受。</p>
    </div>
    <p class="error" v-show="readShow">请先阅读并确认以上条款再进行支付</p>
    <p class="price">567<span>元起</span></p>
    <div class="pay-btn" @click="onPay">立即支付</div>
    <p class="back">返回修改订单</p>
    <is-pay :isShow="ispayShow" @toggle='t'></is-pay>
    <pop-box   :title="'客户告知书'" v-if="notifyShow"
               :width="874"
               :height="650"
               :content="notifyCon"
               @dealClose="handleNoticyClose">></pop-box>
  </div>
</template>

<script>
import isPay from '@/components/TheAliPay'
import popBox from '@/components/BasePopBox'
import { mapGetters } from 'vuex'
export default {
  name: 'TheOrderComfirmPay',
  props: {
    orderInfo: {
      type: Object,
      default: () => {
      }
    }
  },
  data () {
    return {
      notifyShow: false,
      readShow: false,
      payWay: 'zfb',
      read: false,
      ispayShow: false,
      notifyCon: ''
    }
  },
  mounted () {
    this.getNotify()
  },
  methods: {
    goProtocalList(){
      this.$router.push({
        path: '/37duc/product/protocal'
      })
    },
    getNotify () {
      let postData = {
        id: this.productDetails.product.id
      }
      this.$api.product.getNoticeBook(postData).then(res => {
        this.notifyCon = res.data
      })
    },
    handleNoticyClose () {
      this.notifyShow = false
    },
    onPay () {
      if (this.read) {
        let postData = {
          orderSn: this.orderInfo.sn,
          type: 'alipay'
        }
        this.$api.product.orderPay(postData).then(res => {
          if (res.code === 1) {
            window.open(`${process.env.VUE_APP_BASE_URL}${res.data}`)
            this.ispayShow = true
          } else {
            this.$toast.center(res.message)
          }
        })
      } else {
        this.readShow = true
      }
    },
    t () {
      this.ispayShow = false
    }
  },
  computed: {
    ...mapGetters(['productDetails'])
  },
  watch: {
    read (val) {
      if (val) {
        this.readShow = false
      }
    }
  },
  components: {
    isPay,
    popBox
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .order-pay {
    float: right;
    width: 272px;
    background: #fff;
    padding: 20px 20px 30px;
    position: relative;
    .title {
      font-size: 20px;
      color: $color-text;
      font-weight: bold;
      margin-bottom: 26px;
    }
    ul {
      li {
        position: relative;
        padding: 24px 0 15px;
        border-bottom: 1px solid #F1F4F4;
        box-sizing: border-box;
        font-size: 18px;
        color: $color-text;
        label {
          img {
            vertical-align: middle;
            width: 40px;
            line-height: 40px;
            margin-right: 12px;
          }
        }
        input[type="radio"] {
          border-bottom: 1px solid red;
          position: absolute;
          clip: rect(0, 0, 0, 0);
        }
        input[type="radio"] + label:before {
          /*position: absolute;*/
          content: "";
          width: 22px;
          height: 22px;
          background: #FFFFFF;
          border: 1px solid #D5D8D8;
          border-radius: 100%;
          display: inline-block;
          float: right;
          position: absolute;
          top: 34px;
          right: 0;
          box-sizing: border-box;
          cursor: pointer;
        }
        input[type="radio"]:checked + label::before {
          background-color: #01cd78;
          background-clip: content-box;
          width: 22px;
          height: 22px;
          padding: 5px;
        }
      }
    }
    .tips {
      position: relative;
      font-size: 12px;
      color: #B6BBBB;
      line-height: 20px;
      margin-top: 18px;
      .read {
        input[type="checkbox"] {
          border-bottom: 1px solid red;
          position: absolute;
          clip: rect(0, 0, 0, 0);
        }
        input[type="checkbox"] + label:before {
          position: absolute;
          content: "";
          width: 18px;
          height: 18px;
          background: #FFFFFF;
          border: 1px solid #D5D8D8;
          display: inline-block;
          top: 3px;
          left: 0;
          box-sizing: border-box;
          cursor: pointer;
        }
        input[type="checkbox"]:checked + label::before {
          content: "";
          color: #01cd78;
          background: url("../../assets/images/icon_read.png") no-repeat center center;
          padding-left: 4px;
        }
      }
      .protocal {
        padding-left: 26px;
        span {
          cursor: pointer;
          color: #3875E2;
        }
      }
    }
    .error {
      font-size: 14px;
      color: #FF4B4B;
      height: 48px;
      line-height: 48px;
      position: absolute;
      bottom: 175px;
    }
    .price {
      text-align: center;
      font-size: 30px;
      color: $color-active-text;
      margin: 50px 0 30px;
      span {
        font-size: 12px;
        color: #D5D8D8;
      }
    }
    .pay-btn {
      margin: 0 auto;
      @include insured-btn
    }
    .back {
      cursor: pointer;
      text-align: center;
      margin-top: 20px;
      font-size: 14px;
      color: #3875E2;
    }

  }
</style>
